
.header{
	padding: 10px 0px;
	overflow: hidden;
	border-bottom: 1px solid #f3f3f3;
	min-width: 300px;
}
.header .header-logo{
	left: 10px;
	padding-left: 20px;
	cursor: pointer;
}
.header .header-logo img{
	width: 45px;
	float: left;
}
.header ul{
	float: right;
	color: green;
	font-size: 0px;
	line-height: 20px;
	margin-right: 10px;
}
.header ul li{
	display: inline-block;
	font-size: 15px;
	margin: 0 10px;
	cursor: pointer;
}
.header ul li:nth-child(1){
	color: blue;
}
.header ul li:nth-child(2){
	color: brown;
}
.header .search{
	padding:0 20px;
	cursor: pointer;
}
.header .search img{
	float: right;
	width: 25px;
	margin-top: 3px;
}



